import { ChangeDetectorRef, Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { VhQuerySales } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';
import { ViettelService } from '../../../../services/viettelService/viettel.service';
import { ThemeService } from 'src/app/sales/services/theme.service';

@Component({
  selector: 'app-add-address-viettel',
  templateUrl: './add-address-viettel.component.html',
  styleUrls: ['./add-address-viettel.component.scss']
})
export class AddAddressViettelComponent implements OnInit {

  @Input("addAddressViettel") addAddressViettel: boolean = false;
  @Input("id_delivery") id_delivery: any = {};
  @Input('customer') customer: any = {}
  @Output() closeAddAddressViettel = new EventEmitter<any>();
  listProvinceViettel: Array<any> = []
  listDistrictViettel: Array<any> = []
  listWardViettel: Array<any> = []
  loading: boolean = false
  dataAddress: any = { WARDS_NAME: '', DISTRICT_NAME: '', PROVINCE_NAME: '' }
  isLoading: boolean = false
  constructor(
    private lang: LanguageService,
    private vhComponent: VhComponent,
    private vhQuerySales: VhQuerySales,
    private viettelPost: ViettelService,
    private cdRef: ChangeDetectorRef,
    public themeService: ThemeService,
  ) {
    
  }
  addInformation: FormGroup;
  ngOnInit() {
    this.initForm();
  }
  ngAfterViewChecked() {
    this.cdRef.detectChanges()
  }
  initForm() {
    this.addInformation = new FormGroup({
      RECEIVER_FULLNAME: new FormControl({ value: this.customer.RECEIVER_FULLNAME, disabled: true }),
      RECEIVER_PHONE: new FormControl({ value: this.customer.RECEIVER_PHONE, disabled: true }),
      RECEIVER_ADDRESS: new FormControl('', [Validators.required, Validators.maxLength(150)]),
      RECEIVER_PROVINCE: new FormControl('', Validators.required),
      RECEIVER_DISTRICT: new FormControl('', Validators.required),
      RECEIVER_WARD: new FormControl('', Validators.required),
    })


  }
  addInfoAddress() {
    this.vhComponent.alertConfirm("", this.lang.translate("Do you want to create address ViettelPost?"), "", "Ok", this.lang.translate("Cancel"))
      .then(() => {
        this.loading = true
        this.vhQuerySales.addAddress(this.preparePickupAddress(this.id_delivery)).then((res: any) => {
          if (res) {
            this.vhComponent.showToast(2000, this.lang.translate("This address has been created successfully"), "success-toast");
            this.closeAddAddressViettel.emit(true);
          }
          this.loading = false
        })
      }).catch(() => { })
  }
  //dữ liệu lưu db address
  preparePickupAddress(id) {
    let json = this.addInformation.value
    return {
      RECEIVER_FULLNAME: this.customer.RECEIVER_FULLNAME, RECEIVER_PHONE: this.customer.RECEIVER_PHONE,
      RECEIVER_ADDRESS: json.RECEIVER_ADDRESS, RECEIVER_PROVINCE: json.RECEIVER_PROVINCE,
      RECEIVER_DISTRICT: json.RECEIVER_DISTRICT, RECEIVER_WARD: json.RECEIVER_WARD,
      WARDS_NAME: this.dataAddress.WARDS_NAME, DISTRICT_NAME: this.dataAddress.DISTRICT_NAME,
      PROVINCE_NAME: this.dataAddress.PROVINCE_NAME, id_delivery: id, type: 7,
      date: new Date().toISOString(), id_customer: this.customer._id
    }

  }
  handleCancel() {
    this.closeAddAddressViettel.emit(false)
  }
  /**Lấy tỉnh thành phố */
  getProvinceViettel() {
    this.isLoading = true
    this.viettelPost.getProvinceViettel().then((res: any) => {
      this.isLoading = false
      if (res.error) this.vhComponent.alertMessageDesktop('error', res.message)
      else this.listProvinceViettel = res.data
    })
  }
  /**
   * Sự kiện thay đổi tỉnh => reset huyện xã
   * @param event 
   */
  changeProvinceViettel(event) {
    if (event) {
      this.listDistrictViettel = []
      this.listWardViettel = []
      this.addInformation.controls.RECEIVER_DISTRICT.setValue('')
      this.addInformation.controls.RECEIVER_WARD.setValue('')
      this.dataAddress.PROVINCE_NAME = this.listProvinceViettel.find(item => item.PROVINCE_ID == event).PROVINCE_NAME
    }
  }
  /**Lấy quận, huyện */
  getDistrictViettel() {
    this.isLoading = true
    this.viettelPost.getDistrictViettel(this.addInformation.value.RECEIVER_PROVINCE).then((res: any) => {
      this.isLoading = false
      if (res.error) this.vhComponent.alertMessageDesktop('error', res.message)
      else this.listDistrictViettel = res.data.map(item => {
        item.DISTRICT_NAME = this.uppercaseFirstChar(item.DISTRICT_NAME)
        return item
      })
    })
  }
  /**
  * Sự kiện thay đổi huyện => reset xã
  * @param event 
  */
  changeDistrictViettel(event) {
    if (event) {
      this.listWardViettel = []
      this.addInformation.controls.RECEIVER_WARD.setValue('')
      this.dataAddress.DISTRICT_NAME = this.listDistrictViettel.find(item => item.DISTRICT_ID == event).DISTRICT_NAME
    }
  }

  /**Lấy phường, xã */
  getWardViettel() {
    this.isLoading = true
    this.viettelPost.getWardViettel(this.addInformation.value.RECEIVER_DISTRICT).then((res: any) => {
      this.isLoading = false
      if (res.error) this.vhComponent.alertMessageDesktop('error', res.message)
      else this.listWardViettel = res.data.map(item => {
        item.WARDS_NAME = this.uppercaseFirstChar(item.WARDS_NAME)
        return item
      })
    })
  }
  /**
  * Sự kiện thay đổi phường, xã
  * @param event 
  */
  changeWardViettel(event) {
    if (event) this.dataAddress.WARDS_NAME = this.listWardViettel.find(item => item.WARDS_ID == event).WARDS_NAME
  }
  goBack() {
    this.closeAddAddressViettel.emit(false)
  }
  //Viết hoa chữ cái đầu của mỗi từ
  uppercaseFirstChar(str: string) {
    let array = str.toLowerCase().split(' ');
    let newStr = '';
    array.forEach((item) => {
      newStr += item.charAt(0).toUpperCase() + item.slice(1) + ' ';
    });
    return newStr
  }
}
