import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { VhQuerySales } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';
import { ViettelService } from '../../../../services/viettelService/viettel.service';
import { ThemeService } from 'src/app/sales/services/theme.service';

@Component({
  selector: 'app-choose-warehouse-viettel',
  templateUrl: './choose-warehouse-viettel.component.html',
  styleUrls: ['./choose-warehouse-viettel.component.scss']
})
export class ChooseWarehouseViettelComponent implements OnInit {

  @Input("dataWarehouseChooseViettel") dataWarehouseChooseViettel: any = {};
  @Input("chooseWarehouseViettel") chooseWarehouseViettel: boolean = false;
  @Output("cancelModalChooseWarehouse") cancelModalChooseWarehouse = new EventEmitter<any>();
  groupaddressId: string = ''
  shops: Array<any> = [];
  loading: boolean = false
  constructor(
    private lang: LanguageService,
    private vhComponent: VhComponent,
    private vhQuerySales: VhQuerySales,
    private viettelPost: ViettelService,
    public themeService: ThemeService,
  ) {
  }

  ngOnInit() {
    this.getData();
  }
  getData() {
    this.vhComponent.showLoading('', 'transparent-loading').then(() => {
      this.viettelPost.getListInventory(this.dataWarehouseChooseViettel.token).then((res: any) => {
        if (res.error) this.loadingErr(res.message)
        else {
          res.data.forEach(async (item, index) => {
            let listProvince: any = await this.viettelPost.getProvinceViettel()
            if (listProvince && !listProvince.error) item.PROVINCE_NAME = listProvince.data.find(data => data.PROVINCE_ID == item.provinceId).PROVINCE_NAME
            let listDistrict: any = await this.viettelPost.getDistrictViettel(item.provinceId)
            if (listDistrict && !listDistrict.error) item.DISTRICT_NAME = listDistrict.data.find(data => data.DISTRICT_ID == item.districtId).DISTRICT_NAME
            let listWard: any = await this.viettelPost.getWardViettel(item.districtId)
            if (listWard && !listWard.error) item.WARDS_NAME = listWard.data.find(data => data.WARDS_ID == item.wardsId).WARDS_NAME
            item.fullName = item.name + ", " + item.phone + ", " + item.address + ", " + this.uppercaseFirstChar(item.WARDS_NAME) + ", " + this.uppercaseFirstChar(item.DISTRICT_NAME) + ", " + item.PROVINCE_NAME
            if (index == res.data.length - 1) {
              this.shops = res.data
              
              this.vhComponent.hideLoading(0)
            }
          })
        }
      }).catch(err => this.loadingErr(err.message))
    })
  }
  loadingErr(message: string) {
    this.vhComponent.hideLoading(0)
    this.vhComponent.alertMessageDesktop('error', message)
  }
  uppercaseFirstChar(str: string) {
    let array = str.toLowerCase().split(' ');
    let newStr = '';
    array.forEach((item) => {
      newStr += item.charAt(0).toUpperCase() + item.slice(1) + ' ';
    });
    return newStr
  }
  goBack() {
    this.cancelModalChooseWarehouse.emit(false)
  }
  createWarehouse_link() {
    let shop = this.shops.find(item => item.groupaddressId == this.groupaddressId)
    this.vhComponent.alertConfirm("", this.lang.translate("Do you want to connect warehouse ?"), "", "Ok", this.lang.translate("Cancel"))
      .then(() => {
        this.loading = true
        this.vhQuerySales.addDelivery(this.prepareDelivery(shop.groupaddressId, shop.cusId)).then((res: any) => {
          this.vhComponent.alertMessageDesktop('success', this.lang.translate("Connect successfully"));
          this.cancelModalChooseWarehouse.emit(true)
          this.loading = false
        }).catch(err => { this.loading = false; this.lang.translate(err) })
      }).catch(() => { })
  }
  //đóng gói dữ liệu lưu database 
  prepareDelivery(groupaddressId, cusId) {
    return {
      type: 5, active: true, name: "Viettel Post",
      token: this.dataWarehouseChooseViettel.token,
      USERNAME: this.dataWarehouseChooseViettel.USERNAME,
      PASSWORD: this.dataWarehouseChooseViettel.PASSWORD,
      GROUPADDRESS_ID: groupaddressId,
      CUS_ID: cusId
    };
  }
}
