import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { VhQuerySales } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { ViettelService } from '../../../../services/viettelService/viettel.service';
import { ThemeService } from 'src/app/sales/services/theme.service';

@Component({
  selector: 'app-connect-viettel',
  templateUrl: './connect-viettel.component.html',
  styleUrls: ['./connect-viettel.component.scss']
})
export class ConnectViettelComponent implements OnInit {
  @Input("connectViettel") connectViettel: boolean = false;
  @Output() cancelConnectViettel = new EventEmitter<any>();
  PASSWORD: string = ''
  USERNAME: string = ''
  loading: boolean = false
  constructor(
    private viettelPost: ViettelService,
    private vhComponent: VhComponent,
    private vhQuerySales: VhQuerySales,
    public themeService: ThemeService,
  ) { }
  ngOnInit() {
  }

  handleCancel() {
    this.cancelConnectViettel.emit(false);
  }
  /**
   * Lấy token và thực hiện đăng nhập lấy token
   */
  signIn() {
    let delivery: Array<any> = this.vhQuerySales.getlocalDeliverys().filter(item => item.type == 5)
    let data: any = delivery.find(item => item.USERNAME == this.USERNAME && item.PASSWORD == this.PASSWORD)
    if (data) {
      data.exist = true
      this.cancelConnectViettel.emit(data)
    }
    else {
      this.loading = true
      this.viettelPost.getTokenViettel({ USERNAME: this.USERNAME, PASSWORD: this.PASSWORD }).then((res: any) => {
        this.loading = false
        if (res.error) this.vhComponent.alertMessageDesktop('error', res.message)
        else this.cancelConnectViettel.emit({ ...res.data, USERNAME: this.USERNAME, PASSWORD: this.PASSWORD })

      }).catch(err => { this.vhComponent.alertMessageDesktop('error', err); })
    }
  }

}
