import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { VhAlgorithm, VhQuerySales } from 'ionic-vhframeworks';
import { NzModalService } from 'ng-zorro-antd/modal';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-list-address-viettel',
  templateUrl: './list-address-viettel.component.html',
  styleUrls: ['./list-address-viettel.component.scss']
})
export class ListAddressViettelComponent implements OnInit {

  @Input("pickupAdresssViettel") pickupAdresssViettel: boolean = false;
  @Input("id_delivery") id_delivery;
  @Input('customer') id_customer: any = ''
  @Output("cancelListViettel") cancelListViettel = new EventEmitter<any>();
  customer: any = {}
  constructor(
    private lang: LanguageService,
    private vhComponent: VhComponent,
    public vhAlgorighm: VhAlgorithm,
    private vhQuerySales: VhQuerySales,
    private modalService: NzModalService
  ) {

  }
  infoAddress: Array<any> = [];
  status_open: boolean = false;
  ngOnInit() {
    this.getData();
    let customer = this.vhQuerySales.getlocalCustomer(this.id_customer)
    this.customer = { ...customer, RECEIVER_FULLNAME: customer.name, RECEIVER_PHONE: customer.phone }
  }
  getData() {
    this.status_open = false
    this.vhQuerySales.refreshLocalAddresss().then(() => {
      this.status_open = true;
      this.infoAddress = this.vhQuerySales.getlocalAddresss().filter(item => item.type == 7 && item.id_delivery == this.id_delivery && item.id_customer == this.customer._id).sort((prev, next): any => Date.parse(next.date) - Date.parse(prev.date))
    })
    
  }
  trackByFn(index, item) {
    return index;
  }
  goBack() {
    if (this.infoAddress.length) this.cancelListViettel.emit(false);
    else this.cancelListViettel.emit({ _id: '' });
  }
  chooseAddress(item) {
    this.modalService.confirm({
      nzTitle: this.lang.translate("Do you want to use this address ?"),
      nzCancelText: this.lang.translate("Cancel"),
      nzOnOk: () => {
        return new Promise<any>((resolve) => {
          this.vhQuerySales.updateAddress(item._id, { date: new Date().toISOString() }).then((bool) => {
            if (bool) this.cancelListViettel.emit(item);
            else this.cancelListViettel.emit(false);
            resolve(true)
          })
        })
      },
      nzOnCancel: () => { },
    })
  }
  // -----------------modal add address pickup Viettel----------------
  addAddressViettel: boolean = false;
  closeAddAddressViettel(event) {
    if (event) this.getData();
    this.addAddressViettel = false;
  }
  // -----------------modal edit address pickup Viettel----------------
  editAddressViettel: boolean = false;
  dataEditAddressViettel: any;
  editAddress(item) {
    
    this.dataEditAddressViettel = item
    this.editAddressViettel = true;
  }
  closeEditAddressViettel(event) {
    if (event) this.getData();
    this.editAddressViettel = false;
  }

}

